import md5 from 'md5';
import store from '@/store';

class Room {
  // 補足缺少的 key，缺少的 key 會導致資料更新時無法同步渲染
  fullKeys(room) {
    let keys = ['name', 'cover', 'description', 'lastMessage', 'isTag'];
    for (let key of keys) {
      if (!(key in room)) {
        room[key] = null;
      }
    }
    room.unread = room.unread || 0;
    if (!room.roomType) {
      if (room.members.length !== 2) {
        room.roomType = 'group';
      } else {
        let hash = md5(
          room.members
            .map(m => {
              return m._id.toLowerCase();
            })
            .sort((a, b) => {
              return a > b ? 1 : -1;
            })
            .join('_')
        );
        if ((hash, hash === room._id)) {
          room.roomType = 'direct';
        } else {
          room.roomType = 'group';
        }
      }
    }

    if (!room.pref) {
      room.pref = {};
    }
    room.pref = {
      hidden: room.pref.hidden && store.state.config.list.hidden,
      sticky: room.pref.sticky && store.state.config.list.sticky,
      folder:
        room.pref.folder && store.state.config.list.sticky
          ? room.pref.folder
          : null,
      muted: room.pref.muted || false,
      tags: room.pref.tags || []
    };

    // 置頂
    if (
      room.pref.sticky &&
      !store.state.roomStickyList.find(_room => _room._id === room._id)
    ) {
      store.state.roomStickyList.push(room);
      store.commit('setRoomStickyList', store.state.roomStickyList);
    }

    // 資料夾
    if (room.pref.folder) {
      this.addRoomFolderList(room.pref.folder);
    }

    // 客製
    if (store.state.config.special.renderRoomDescription) {
      room.description = store.state.config.special.renderRoomDescription(
        room.description
      );
    }
  }

  // 新增房間資料夾
  addRoomFolderList(folder) {
    if (
      store.state.roomFolderList.find(item => {
        return item.id === folder;
      })
    ) {
      return;
    }

    store.state.roomFolderList.push({
      id: folder,
      unread: 0,
      sortIdx: 0,
      open: false
    });
  }

  deleteRoomFolderList(folder) {
    let idx = store.state.roomFolderList.findIndex(item => {
      return item.id === folder;
    });

    if (~idx) {
      store.state.roomFolderList.splice(idx, 1);
      store.state.roomList.forEach(room => {
        if (room.pref.folder === folder) {
          this.setRoomFolder(room, null);
        }
      });
    }
  }

  setRoomFolder(room, folder) {
    if (!folder) {
      folder = null;
    }
    room.pref.folder = folder;
    store.dispatch('updateRoomPref', {
      roomId: room._id,
      pref: {
        folder: folder
      }
    });
  }
}

export default Room;



