import Vue from 'vue';
import Router from 'vue-router';
import Home from '@/components/Home/index.vue';

Vue.use(Router);

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '*',
      name: 'Home',
      component: Home
    }
  ]
});



