import { ResizeObserver } from 'vue-resize';

export default {
  name: 'ResizeDiv',
  props: [],
  data() {
    return {};
  },
  components: {
    ResizeObserver
  },
  mounted() {},
  methods: {
    resize() {
      this.$emit('resize');
    }
  },
  computed: {}
};



