import Scrollbar from '@/components/Components/Scrollbar/index.vue';

export default {
  name: 'DivEditor',
  props: ['placeholder', 'text', 'limitLemgth'],
  components: {
    Scrollbar
  },
  data() {
    return {
      hasInputEvent: false // 有沒有 input 的 event，天殺的IE
    };
  },
  watch: {},
  mounted() {
    // 處理 IE 把網址變超連結的問題
    try {
      document.execCommand('AutoUrlDetect', false, false);
    } catch (e) {}
  },
  methods: {
    setText(text) {
      this.$refs.editor.innerHTML = text;
      this.$emit('change', text);
    },
    changeText() {
      this.$emit('change', this.$refs.editor.innerHTML);
    },
    inputForIE() {
      if (!this.hasInputEvent) {
        setTimeout(() => {
          if (!this.hasInputEvent) {
            this.changeText();
          }
        }, 1);
      }
    },
    input() {
      this.hasInputEvent = true;
      this.changeText();
    },
    keydowm(e) {
      if (
        this.$refs.editor.innerText.length >= this.limitLemgth &&
        !e.ctrlKey &&
        !~[8, 13, 17, 37, 38, 39, 40, 46].indexOf(e.keyCode)
      ) {
        e.preventDefault();
      }
    },
    keyEnter(e) {
      if (!e.shiftKey && !this.$store.state.isMobile) {
        e.preventDefault();
        this.$emit('keyEnter');
      } else {
        this.$emit('keyShiftEnter');
      }
    },
    paste(e) {
      e.preventDefault();
      let text = '';
      if (window.clipboardData && window.clipboardData.setData) {
        // IE
        text = window.clipboardData.getData('text');
      } else {
        text =
          (e.originalEvent || e).clipboardData.getData('text/plain') ||
          prompt('');
      }
      if (this.$refs.editor.innerText.length + text.length > this.limitLemgth) {
        return;
      }
      if (document.body.createTextRange) {
        let textRange;
        if (document.selection) {
          textRange = document.selection.createRange();
        } else if (window.getSelection) {
          let sel = window.getSelection();
          let range = sel.getRangeAt(0);

          // 創建臨時元素，使得TextRange可以移動到正確的位置
          let tempEl = document.createElement('span');
          tempEl.innerHTML = '&#FEFF;';
          range.deleteContents();
          range.insertNode(tempEl);
          textRange = document.body.createTextRange();
          textRange.moveToElementText(tempEl);
          tempEl.parentNode.removeChild(tempEl);
        }
        textRange.text = text;
        textRange.collapse(false);
        textRange.select();
      } else {
        // Chrome
        document.execCommand('insertText', false, text);
      }
    },
    insertText(text) {
      if (this.$refs.editor.innerText.length + text.length > this.limitLemgth) {
        return;
      }
      this.$refs.editor.focus();

      if (window.getSelection) {
        let sel = window.getSelection();
        if (sel.getRangeAt && sel.rangeCount) {
          let range = sel.getRangeAt(0);
          range.deleteContents();

          let el = document.createElement('div');
          el.innerHTML = text;
          let frag = document.createDocumentFragment();
          let node;
          let lastNode;
          while ((node = el.firstChild)) {
            lastNode = frag.appendChild(node);
          }
          range.insertNode(frag);

          if (lastNode) {
            range = range.cloneRange();
            range.setStartAfter(lastNode);
            range.collapse(true);
            sel.removeAllRanges();
            sel.addRange(range);
          }
        }
      } else if (document.selection && document.selection.type !== 'Control') {
        document.selection.createRange().pasteHTML(text);
      }
      this.changeText();
      if (this.$store.state.isMobile) {
        this.$refs.editor.blur();
      }
    }
  }
};



