import { mapActions } from 'vuex';
import Modal from '@/components/Components/Modal/index.vue';
import axios from 'axios';

export default {
  name: 'PaymentCheck',
  props: ['data'],
  data() {
    return {
      note: '',
      iframe: ''
    };
  },
  components: {
    Modal
  },
  directives: {},
  computed: {},
  mounted() {},
  methods: {
    pay() {
      let w = window.open('');
      axios({
        method: 'post',
        url: this.data.paymentApi,
        data: {
          note: this.note,
          chatUserId: this.$store.state.client.client,
          requestId: this.data.id
        }
      })
        .then(resp => {
          // this.iframe = resp.data.data.payment_url;
          this.close();
          w.location = resp.data.data.payment_url;
          // window.open(resp.data.data.payment_url);
        })
        .catch(e => {
          w.close();
          this.alert(e.response.data.message);
        });
    },
    close() {
      this.$emit('close');
    },
    ...mapActions(['alert'])
  }
};



