import { mapActions } from 'vuex';
import Modal from '@/components/Components/Modal/index.vue';

export default {
  name: 'RoomInfoEditor',
  props: ['room'],
  data() {
    return {
      roomData: {
        id: null,
        name: null,
        description: null,
        cover: null
      }
    };
  },
  components: {
    Modal
  },
  mounted() {
    if (this.room) {
      this.roomData.id = this.room.id;
      this.roomData.name = this.room.name;
      this.roomData.description = this.room.description;
      this.roomData.cover = this.room.cover;
    }
  },
  methods: {
    async save() {
      await this.updateRoomInfo({
        roomId: this.roomData.id,
        roomInfo: {
          name: this.roomData.name,
          description: this.roomData.description,
          cover: this.roomData.cover
        }
      });

      this.$emit('close');
    },
    cancel() {
      this.$emit('close');
    },
    ...mapActions(['updateRoomInfo'])
  }
};



