import StaticMap from 'vue-static-map';

export default {
  name: 'UrlPreview',
  props: ['urlExtra'],
  data() {
    return {};
  },
  components: {
    StaticMap
  },
  computed: {
    isVideo() {
      let ID = '';
      let url = this.urlExtra.link
        .replace(/(>|<)/gi, '')
        .split(/(vi\/|v=|\/v\/|youtu\.be\/|\/embed\/)/);
      if (url[2] !== undefined) {
        ID = url[2].split(/[^0-9a-z_-]/i);
        ID = ID[0];
      } else {
        ID = null;
      }
      return ID;
    }
  },
  mounted() {},
  methods: {}
};



