import { mapActions } from 'vuex';
import Modal from '@/components/Components/Modal/index.vue';
import Scrollbar from '@/components/Components/Scrollbar/index.vue';
import RoomListItem from '@/components/RoomList/RoomListItem/index.vue';

export default {
  name: 'RoomChatForwardRooms',
  data() {
    return {};
  },
  components: {
    Modal,
    Scrollbar,
    RoomListItem
  },
  computed: {
    listScroll() {
      return this.$refs.list;
    }
  },
  methods: {
    onListScroll() {
      if (
        this.$store.state.roomList &&
        !this.$store.state.roomListLoading && // 正在讀取中
        this.$store.state.roomListLoader && // 有更多
        this.listScroll.scrollBottom() < 10
      ) {
        this.getRoomList();
      }
    },
    roomClick(room) {
      let idx = this.$store.state.forward.rooms.findIndex(_room => {
        return _room.id === room.id;
      });
      if (idx < 0) {
        if (this.$store.state.forward.rooms.length < 10) {
          this.$store.state.forward.rooms.push(room);
        }
      } else {
        this.$store.state.forward.rooms.splice(idx, 1);
      }
    },
    forwardChecked(room) {
      return !!this.$store.state.forward.rooms.find(_room => {
        return _room.id === room.id;
      });
    },
    close() {
      this.$emit('close');
    },
    ...mapActions(['getRoomList', 'sendForward'])
  },
  mounted() {}
};



