import { mapActions } from 'vuex';
import ClickOutside from 'vue-click-outside';
import lib from '@/lib';

export default {
  name: 'Recorder',
  props: ['action'],
  data() {
    return {
      checkNavigator: false, // 確認是否支援錄音
      actionName: 'recorder',
      startTime: 0, // 開始錄音的時間(ms)
      recordSec: 0, // 錄音持續時間(s))
      recStatus: false, // 錄音狀態，是否正在錄音
      getUserMedia: null, // 瀏覽器開關錄音錄影的 function
      timer: 0, // 錄音計時器
      context: null, // 麥克風對象
      dataArray: null, // 取得音量用
      analyser: null, // 取得音量用
      sumDataArray: 0, // 音量
      recorder: null, // 錄音機本體
      audioes: []
    };
  },
  components: {},
  directives: {
    ClickOutside
  },
  computed: {
    _secMin() {
      return ~~(this.recordSec / 60);
    },
    _secSec() {
      return (this.recordSec % 60).toString().padStart(2, '0');
    },
    formatSec() {
      return `${this._secMin}:${this._secSec}`;
    },
    frequencyStyle() {
      return {
        width: this.sumDataArray + 'px',
        height: this.sumDataArray + 'px'
      };
    }
  },
  mounted() {
    // this.checkNavigator = lib.recorder.init();
    lib.recorder.init();
    this.checkNavigator = true;
  },
  methods: {
    open() {
      this.$emit('open', this.actionName);
    },
    close() {
      if (this.action === this.actionName) {
        this.cancel();
        this.$emit('close');
      }
    },
    start(stream) {
      // 錄音
      lib.recorder.start(stream => {
        this.recStatus = true;
        this.startTime = Date.now();

        // 取得音量用的一陀 code
        this.analyser = lib.recorder.context.createAnalyser();
        lib.recorder.microphone.connect(this.analyser);
        this.analyser.fftSize = 512;
        this.dataArray = new Uint8Array(this.analyser.fftSize);
        this.analyser.getByteFrequencyData(this.dataArray);
        this.requestAnimationFrame();
      });
    },
    requestAnimationFrame() {
      // 計算錄音時間
      this.recordSec = ~~((Date.now() - this.startTime) / 1000);

      // 取得音量
      this.analyser.getByteFrequencyData(this.dataArray);
      this.sumDataArray = (
        this.dataArray.slice(0, 50).reduce((p, c) => p + c, 0) / 200 +
        72
      ).toFixed(2);

      // loop
      this.timer = window.requestAnimationFrame(this.requestAnimationFrame);

      let limitSeconds =
        this.$store.state.config.chat.actions.recorder.extra &&
        this.$store.state.config.chat.actions.recorder.extra.limitSeconds;
      if (limitSeconds && this.recordSec > limitSeconds) {
        this.stop();
      }
    },
    stop() {
      this.close();
      lib.recorder.getBlob(
        blob => {
          this.$emit('outputScrollToBottom');
          this.uploadFile(blob);
        },
        error => {
          this.$log.error('[Recorder][getBlob]', error);
        }
      );
    },
    record() {
      if (this.recStatus) {
        // 完成+送出
        this.stop();
      } else {
        this.start();
      }
    },
    cancel() {
      if (this.recStatus) {
        this.recStatus = false;
        this.recordSec = 0;

        window.cancelAnimationFrame(this.timer);

        // 音量的
        this.sumDataArray = 0;
        this.dataArray = null;

        // 錄音的
        lib.recorder.stop();
      }
    },
    ...mapActions(['alert', 'uploadFile'])
  }
};



