import store from '@/store';
import i18n from '@/i18n';
import RecorderWorker from './Recorder.worker.js';
window.MediaRecorder = require('audio-recorder-polyfill');

class Recorder {
  config;
  context;
  microphone; // 媒体流音频源
  processor; // js音频處理器
  stream;
  startOnSuccess;
  successCallback;
  errorCallback;
  realTimeWorker;

  init(config = {}) {
    this.config = Object.assign(
      {
        sampleRate: 44100, // 採樣頻率，44100Hz 為標準 MP3 採樣頻率
        bitRate: 128 // 比特率，128kbps 為標準 MP3 質量
      },
      config
    );

    window.AudioContext = window.AudioContext || window.webkitAudioContext;

    return !!navigator.getUserMedia;
  }

  getPermission() {
    if (MediaRecorder.notSupported) {
      store.dispatch('alert', i18n.t('Failed to get permission'));
    }
    navigator.mediaDevices
      .getUserMedia({ audio: true, video: false })
      .then(stream => {
        this.stream = stream;
        this.context = new AudioContext();
        this.microphone = this.context.createMediaStreamSource(stream); // 媒体流音频源
        this.processor = this.context.createScriptProcessor(16384, 1, 1); // js音频處理器
        this.realTimeWorker = new RecorderWorker();

        this.config.sampleRate = this.context.sampleRate;

        this.processor.onaudioprocess = e => {
          // 監聽音頻錄製過程
          let array = e.inputBuffer.getChannelData(0);
          this.realTimeWorker.postMessage({ cmd: 'encode', buf: array });
        };

        this.realTimeWorker.onmessage = e => {
          switch (e.data.cmd) {
            case 'init':
              this.startRecord(stream);
              break;
            case 'end':
              if (this.successCallback) {
                let blob = new Blob(e.data.buf, { type: 'audio/mp3' });
                this.successCallback(blob);
              }
              break;
            case 'error':
              if (this.errorCallback) {
                this.errorCallback(e.data.error);
              }
              break;
            default:
              break;
          }
        };

        this.realTimeWorker.postMessage({
          cmd: 'init',
          config: {
            sampleRate: this.config.sampleRate,
            bitRate: this.config.bitRate
          }
        });
      });
  }

  start(startOnSuccess) {
    this.startOnSuccess = startOnSuccess;
    this.getPermission();
  }

  startRecord(stream) {
    if (this.processor && this.microphone) {
      this.microphone.connect(this.processor);
      this.processor.connect(this.context.destination);

      this.startOnSuccess(stream);
    }
  }

  stop() {
    if (this.processor && this.microphone) {
      this.microphone.disconnect();
      this.processor.disconnect();
      if (this.stream) {
        var tracks = this.stream.getTracks();
        for (var i = 0; i < tracks.length; i++) {
          tracks[i].stop();
        }
        this.stream = null;
      }
    }
  }

  getBlob(onSuccess, onError) {
    this.successCallback = onSuccess;
    this.errorCallback = onError;
    this.realTimeWorker.postMessage({ cmd: 'finish' });
  }
}

export default Recorder;



