import { mapActions } from 'vuex';
export default {
  name: 'VideoUpload',
  props: ['action'],
  data() {
    return {
      actionName: 'video'
    };
  },
  components: {},
  directives: {},
  computed: {},
  methods: {
    selectFiles(event) {
      this.$emit('outputScrollToBottom');
      this.uploadFiles(event);
    },
    ...mapActions(['uploadFiles'])
  }
};



