import ResizeDiv from '@/components/Components/ResizeDiv/index.vue';

export default {
  name: 'Scrollbar',
  props: {
    autoHide: {
      type: Boolean,
      default: true
    }
  },
  components: {
    ResizeDiv
  },
  data() {
    return {
      scrollWidth: 0,
      barY: {
        show: false, // 是否繪製卷軸
        hide: true, // 是否隱藏卷軸
        hideTimer: null,
        height: 0,
        top: 0
      },
      resizeY: {
        timer: null,
        isBottom: false
      }
    };
  },
  mounted() {
    this.init();
  },
  computed: {
    innerStyle() {
      return {
        width: `calc(100% + ${this.scrollWidth}px)`,
        marginRight: `-${this.scrollWidth}px`
      };
    },
    barYStyle() {
      return {
        height: this.barY.height + 'px',
        top: this.barY.top + 'px'
      };
    }
  },
  methods: {
    init() {
      this.getScrollWidth();
      this.drawScrollBar();
    },
    getScrollWidth() {
      let outerEle = document.createElement('div');
      let innerEle = document.createElement('div');
      outerEle.style.position = 'absolute';
      innerEle.style.overflow = 'scroll';
      outerEle.appendChild(innerEle);
      document.body.appendChild(outerEle);
      this.scrollWidth = outerEle.clientWidth + 20;
      document.body.removeChild(outerEle);
    },
    drawScrollBar() {
      if (!this.$refs.box) {
        return;
      }
      let boxHeight = this.$refs.box.clientHeight;
      let contentHeight = this.$refs.content.clientHeight;
      this.barY.show = boxHeight < contentHeight;
      if (this.barY.show) {
        this.barY.height = Math.max(
          15,
          boxHeight * (boxHeight / contentHeight)
        );
        this.scroll();
      }
    },
    scroll() {
      if (!this.$refs.box) {
        return;
      }
      let boxHeight = this.$refs.box.clientHeight;
      let contentHeight = this.$refs.content.clientHeight;
      this.barY.top =
        ((boxHeight - this.barY.height) / (contentHeight - boxHeight)) *
        this.$refs.box.scrollTop;

      this.showScroll();

      this.$emit('scroll');
    },
    showScroll() {
      if (this.hideTimer) {
        clearTimeout(this.hideTimer);
      }
      this.barY.hide = false;
      this.hideTimer = setTimeout(() => {
        this.barY.hide = true;
      }, 1000);
    },
    resize() {
      if (!this.$refs.box) {
        return;
      }
      // 檢查卷軸是不是在底部，是的話，resize重劃完卷軸後要捲到底部
      if (!this.resizeY.timer && this.barY.show) {
        this.resizeY.isBottom = this.scrollBottom() < 30;
        clearTimeout(this.resizeY.timer);
        this.resizeY.timer = setTimeout(() => {
          clearTimeout(this.resizeY.timer);
          this.resizeY.timer = null;
        }, 1000);
      }

      this.drawScrollBar();
      this.barY.hide = true;

      if (this.resizeY.isBottom) {
        this.$refs.box.scrollTop = this.$refs.content.clientHeight;
      }
    },
    contentResize() {
      this.drawScrollBar();
    },
    scrollTop(top = null) {
      if (top !== null) {
        this.$refs.box.scrollTop = top;
      }
      return this.$refs.box.scrollTop;
    },
    scrollBottom(bottom = null) {
      if (!this.$refs.box) {
        return 0;
      }
      if (bottom === 0) {
        this.$refs.box.scrollTop = this.$refs.content.clientHeight;
      } else if (bottom !== null) {
        this.$refs.box.scrollTop =
          this.$refs.content.clientHeight -
          this.$refs.box.clientHeight -
          bottom;
      }
      return (
        this.$refs.content.clientHeight -
        this.$refs.box.scrollTop -
        this.$refs.box.clientHeight
      );
    },
    scrollElement(element) {
      this.scrollTop(element.offsetTop - this.$refs.content.offsetTop - 50);
    },
    hasScroll() {
      return this.barY.show;
    }
  }
};



