import EXIF from 'exif-js';
import store from '@/store';

class Upload {
  async getThumbnailFromImage(file) {
    let thumbnailSize =
      store.state.config.chat.actions.image.extra.thumbnailSize;
    let data = URL.createObjectURL(file);
    return new Promise((resolve, reject) => {
      let canvas = document.createElement('canvas');
      let canvasThumbnail = document.createElement('canvas');

      let ctx = canvas.getContext('2d');
      let ctxThumbnail = canvasThumbnail.getContext('2d');

      let img = new Image();
      img.onload = () => {
        EXIF.getData(img, function() {
          let orientation = EXIF.getTag(this, 'Orientation');

          let p = Math.min(1, thumbnailSize / Math.max(img.width, img.height));
          ctx.save();
          ctxThumbnail.save();
          let width = img.width;
          let height = img.height;
          let widthThumbnail = img.width * p;
          let heightThumbnail = img.height * p;
          if (orientation && orientation > 4) {
            canvas.width = height;
            canvas.height = width;
            canvasThumbnail.width = heightThumbnail;
            canvasThumbnail.height = widthThumbnail;
          } else {
            canvas.width = width;
            canvas.height = height;
            canvasThumbnail.width = widthThumbnail;
            canvasThumbnail.height = heightThumbnail;
          }

          switch (orientation) {
            case 2:
              // horizontal flip
              ctx.translate(width, 0);
              ctx.scale(-1, 1);
              ctxThumbnail.translate(widthThumbnail, 0);
              ctxThumbnail.scale(-1, 1);
              break;
            case 3:
              // 180° rotate left
              ctx.translate(width, height);
              ctx.rotate(Math.PI);
              ctxThumbnail.translate(widthThumbnail, heightThumbnail);
              ctxThumbnail.rotate(Math.PI);
              break;
            case 4:
              // vertical flip
              ctx.translate(0, height);
              ctx.scale(1, -1);
              ctxThumbnail.translate(0, heightThumbnail);
              ctxThumbnail.scale(1, -1);
              break;
            case 5:
              // vertical flip + 90 rotate right
              ctx.rotate(0.5 * Math.PI);
              ctx.scale(1, -1);
              ctxThumbnail.rotate(0.5 * Math.PI);
              ctxThumbnail.scale(1, -1);
              break;
            case 6:
              // 90° rotate right
              ctx.rotate(0.5 * Math.PI);
              ctx.translate(0, -height);
              ctxThumbnail.rotate(0.5 * Math.PI);
              ctxThumbnail.translate(0, -heightThumbnail);
              break;
            case 7:
              // horizontal flip + 90 rotate right
              ctx.rotate(0.5 * Math.PI);
              ctx.translate(width, -height);
              ctx.scale(-1, 1);
              ctxThumbnail.rotate(0.5 * Math.PI);
              ctxThumbnail.translate(widthThumbnail, -heightThumbnail);
              ctxThumbnail.scale(-1, 1);
              break;
            case 8:
              // 90° rotate left
              ctx.rotate(-0.5 * Math.PI);
              ctx.translate(-width, 0);
              ctxThumbnail.rotate(-0.5 * Math.PI);
              ctxThumbnail.translate(-widthThumbnail, 0);
              break;
          }

          ctx.drawImage(img, 0, 0, width, height);
          ctxThumbnail.drawImage(img, 0, 0, widthThumbnail, heightThumbnail);
          resolve({
            originalFile: dataURItoBlob(canvas.toDataURL()),
            thumbnailFile: dataURItoBlob(canvasThumbnail.toDataURL()),
            fileWidth: canvasThumbnail.width,
            fileHeight: canvasThumbnail.height
          });
        });
      };
      img.src = data;
    });
  }
  async getThumbnailFromVideo(file) {
    let thumbnailSize =
      store.state.config.chat.actions.video.extra.thumbnailSize;
    let data = URL.createObjectURL(file);
    return new Promise((resolve, reject) => {
      let iPhoneVersion = 0;
      if (navigator.userAgent.match(/iPhone/i)) {
        let v = navigator.appVersion.match(/OS (\d+)_(\d+)_?(\d+)?/);
        let d = {
          status: true,
          version: parseInt(v[1], 10),
          info:
            parseInt(v[1], 10) +
            '.' +
            parseInt(v[2], 10) +
            '.' +
            parseInt(v[3] || 0, 10)
        };
        iPhoneVersion = d.version;
      }

      let canvas = document.createElement('canvas');
      let ctx = canvas.getContext('2d');
      let video = document.createElement('video');
      document.body.appendChild(video);

      video.setAttribute('playsinline', true);
      video.setAttribute('controls', true);
      video.volume = 0;
      video.style.position = 'absolute';
      video.style.left = '-10000px';
      video.addEventListener('loadeddata', () => {
        video.pause();
        let p = Math.min(
          1,
          thumbnailSize / Math.max(video.videoWidth, video.videoHeight)
        );
        canvas.width = video.videoWidth * p;
        canvas.height = video.videoHeight * p;

        ctx.save();
        if (iPhoneVersion >= 11) {
          ctx.rotate(0.5 * Math.PI);
          ctx.translate(0, -canvas.width);
          ctx.drawImage(video, 0, 0, canvas.height, canvas.height);
        } else {
          ctx.drawImage(video, 0, 0, canvas.width, canvas.height);
        }

        ctx.restore();
        // ctx.drawImage(video, 0, 0, canvas.width, canvas.height);
        // ctx.fillStyle = 'rgba(0, 0, 0, 0.3)';
        // ctx.fillRect(0, 0, canvas.width, canvas.height);

        // ctx.fillStyle = 'rgba(255, 255, 255, 0.7)';
        // ctx.beginPath();
        // ctx.moveTo(canvas.width / 2 - 20, canvas.height / 2 - 20);
        // ctx.lineTo(canvas.width / 2 - 20, canvas.height / 2 + 20);
        // ctx.lineTo(canvas.width / 2 + 20, canvas.height / 2);
        // ctx.lineTo(canvas.width / 2 - 20, canvas.height / 2 - 20);
        // ctx.closePath();
        // ctx.fill();
        // ctx.stroke();
        document.body.removeChild(video);
        resolve({
          originalFile: file,
          thumbnailFile: dataURItoBlob(canvas.toDataURL()),
          fileWidth: canvas.width,
          fileHeight: canvas.height,
          duration: video.duration
        });
      });
      video.src = data;
      video.play();
    });
  }
  async getInfoFromAudio(file) {
    let data = URL.createObjectURL(file);
    return new Promise((resolve, reject) => {
      let audio = document.createElement('audio');
      document.body.appendChild(audio);

      audio.setAttribute('playsinline', true);
      audio.setAttribute('controls', true);
      audio.volume = 0;
      audio.style.position = 'absolute';
      audio.style.left = '-10000px';
      audio.addEventListener('loadeddata', () => {
        audio.pause();
        document.body.removeChild(audio);
        resolve({
          duration: audio.duration
        });
      });
      audio.src = data;
      audio.load();
      audio.play();
    });
  }
  b64ToBlob(b64) {
    return dataURItoBlob(b64);
  }
}
let dataURItoBlob = dataURI => {
  let byteString = atob(dataURI.split(',')[1]);
  let mimeString = dataURI
    .split(',')[0]
    .split(':')[1]
    .split(';')[0];
  let ab = new ArrayBuffer(byteString.length);
  let ia = new Uint8Array(ab);
  for (let i = 0; i < byteString.length; i++) {
    ia[i] = byteString.charCodeAt(i);
  }
  let blob = new Blob([ab], { type: mimeString });
  return blob;
};
export default Upload;



