import moment from 'moment';
import PaymentCheck from './PaymentCheck/index.vue';
export default {
  name: 'RequestPayment',
  props: ['extra'],
  data() {
    return {
      data: null,
      isShowCheck: false
    };
  },
  components: {
    PaymentCheck
  },
  computed: {},
  mounted() {
    this.data = JSON.parse(this.extra);
    this.data.time = moment(this.data.time).format('YYYY-MM-DD HH:mm');
  },
  methods: {
    showCheck() {
      this.isShowCheck = true;
    },
    hideCheck() {
      this.isShowCheck = false;
    }
  }
};



