import { mapGetters } from 'vuex';
import Swiper from 'swiper/dist/js/swiper.js';
import FileLoader from '@/components/Components/FileLoader/index.vue';
import localForage from 'localforage';
import lib from '@/lib';

export default {
  name: 'RoomChatOutputSilder',
  props: ['default'],
  data() {
    return {
      swiperMain: null,
      swiperThumbs: null,
      tools: {
        hide: false
      },
      medias: [],
      focusIdx: null,
      downloadLink: null,
      downloadLinkTimer: null
    };
  },
  components: {
    FileLoader
  },
  computed: {
    focusMedia() {
      return this.medias[this.focusIdx];
    },
    fileName() {
      if (this.focusMedia.extra) {
        let extra = JSON.parse(this.focusMedia.extra);
        return `${this.focusMedia.message || this.focusMedia.id}${
          extra.fileExtension ? '.' + extra.fileExtension : ''
        }`;
      }
      return this.focusMedia.id;
    },
    ...mapGetters(['roomChat'])
  },
  mounted() {
    window.addEventListener('keyup', this.keyup);

    if (this.roomChat) {
      this.medias = this.roomChat.messages.filter(_message => {
        return (
          ~['image', 'video'].indexOf(_message.messageType) && !_message.status
        );
      });
    } else {
      this.medias = [];
      this.close();
    }

    if (this.default) {
      this.focusIdx =
        this.medias.findIndex(media => {
          return media.id === this.default;
        }) || 0;
    }

    this.$nextTick(() => {
      this.swiperInit();
    });

    setTimeout(() => {
      this.tools.hide = true;
    }, 1500);
  },
  methods: {
    keyup(e) {
      // ESC key
      if (e.keyCode === 27) {
        this.close();
      }
    },
    swiperInit() {
      if (!this.swiperMain) {
        this.swiperMain = new Swiper('.sliderMain .swiper-container', {
          sensitivity: 200,
          navigation: {
            nextEl: '.swiper-next',
            prevEl: '.swiper-prev'
          },
          zoom: {
            maxRatio: 5
          },
          on: {
            slideChange: () => {
              this.focusIdx = this.swiperMain.activeIndex;
              let videos = this.$refs.sliderMain.getElementsByTagName('video');
              for (let video of videos) {
                video.pause();
              }
              this.getDownloadLink();
            },
            zoomChange: scale => {
              if (scale > 1) {
                this.tools.hide = true;
              }
            }
          }
        });
        this.swiperThumbs = new Swiper('.sliderThumbs .swiper-container', {
          spaceBetween: 10,
          centeredSlides: true,
          slidesPerView: 'auto',
          touchRatio: 0.2,
          slideToClickedSlide: true
        });
        this.swiperMain.controller.control = this.swiperThumbs;
        this.swiperThumbs.controller.control = this.swiperMain;
      }
      this.swiperMain.slideTo(this.focusIdx, 1);
    },
    close() {
      this.$emit('close');
    },
    toggleToolsHide() {
      if (this.swiperMain.zoom.scale > 1) {
        this.tools.hide = true;
      } else {
        this.tools.hide = !this.tools.hide;
      }
    },
    async getDownloadLink() {
      if (this.downloadLinkTimer) {
        clearTimeout(this.downloadLinkTimer);
        this.downloadLinkTimer = null;
      }
      let fileId = this.focusMedia.originalUrl.split('/').pop();
      let data = await localForage.getItem(`IMKit-${fileId}`);
      if (data) {
        let blob = lib.upload.b64ToBlob(data);
        this.downloadLink = URL.createObjectURL(blob);
      } else {
        this.downloadLink = null;
        this.downloadLinkTimer = setTimeout(this.getDownloadLink, 1000);
      }
    }
  },
  beforeDestroy() {
    window.removeEventListener('keyup', this.keyup);
  }
};



