import axios from 'axios';
import { mapActions } from 'vuex';
import Modal from '@/components/Components/Modal/index.vue';
import Radio from '@/components/Components/Radio/index.vue';

export default {
  name: 'PaymentRequest',
  props: ['action'],
  data() {
    return {
      actionName: 'paymentRequest',
      currency: '',
      amount: '',
      paymentBy: '',
      note: ''
    };
  },
  components: {
    Modal,
    Radio
  },
  directives: {},
  computed: {
    config() {
      return this.$store.state.config.chat.actions.paymentRequest;
    },
    paymentByList() {
      return this.config.extra.paymentBy.filter(_by => {
        return (
          !_by.currencies ||
          _by.currencies.length === 0 ||
          ~_by.currencies.indexOf(this.currency)
        );
      });
    },
    amountFixed() {
      return this.currency === 'TWD' ? 0 : 2;
    }
  },
  mounted() {},
  methods: {
    open() {
      this.$emit('open', this.actionName);
    },
    close() {
      if (this.action === this.actionName) {
        this.currency = '';
        this.amount = '';
        this.note = '';
        this.$emit('close');
      }
    },
    async send() {
      if (!this.currency || !this.amount || this.amount === '0') {
        this.alert(this.$t('Please enter currency and amount'));
        return;
      }
      if (
        !this.paymentBy ||
        !~this.paymentByList.findIndex(_by => this.paymentBy === _by.key)
      ) {
        this.alert(this.$t('Please select paymentBy'));
        return;
      }
      this.$emit('outputScrollToBottom');
      axios({
        method: 'post',
        url: this.config.extra.requestApi,
        data: {
          chatRoomId: this.$store.state.roomActive._id,
          chatUserId: this.$store.state.client.client,
          amount: this.amount,
          currency: this.currency,
          paymentBy: this.paymentBy,
          note: this.note
        }
      })
        .then(resp => {
          this.close();
        })
        .catch(e => {
          this.alert(e.response.data.message);
        });
    },
    currencyOnChange() {
      this.amountOnChange();
    },
    amountOnChange() {
      let amount = Math.max(0, Number.parseFloat(this.amount));

      if (this.currency === 'USD') {
        amount = Math.min(amount, 30);
      }

      this.amount = amount.toFixed(this.amountFixed);
    },
    amountOnFocus() {
      if (Number.parseFloat(this.amount) === 0) {
        this.amount = '';
      }
    },
    ...mapActions(['alert'])
  }
};



