import moment from 'moment';

export default {
  name: 'Payment',
  props: ['extra'],
  data() {
    return {
      data: null
    };
  },
  components: {},
  computed: {},
  mounted() {
    this.data = JSON.parse(this.extra);
    this.data.time = moment(this.data.time).format('YYYY-MM-DD HH:mm');
  },
  methods: {}
};



