import { mapActions } from 'vuex';
import qs from 'qs';
import Avatar from '@/components/Components/Avatar/index.vue';
import AvatarModal from '@/components/Components/AvatarModal/index.vue';
import Scrollbar from '@/components/Components/Scrollbar/index.vue';
import RoomInfoEditor from '@/components/RoomInfo/RoomInfoEditor/index.vue';
import RoomInfoInvite from '@/components/RoomInfo/RoomInfoInvite/index.vue';

export default {
  name: 'RoomInfo',
  data() {
    return {
      showRoomInfoEditor: false,
      showRoomInfoInvite: false,
      avatarInfo: null
    };
  },
  components: {
    Avatar,
    AvatarModal,
    Scrollbar,
    RoomInfoEditor,
    RoomInfoInvite
  },
  computed: {
    isGroup() {
      return this.room.roomType === 'group';
    },
    info() {
      return this.$store.state.config.info;
    },
    edit() {
      return this.$store.state.config.info.edit;
    },
    room() {
      return this.$store.state.roomActive;
    },
    createdTime() {
      return this.$i18n.t('Create at {date}', {
        date: this.$options.filters.dateFormat(
          this.room.createdTimeMS,
          'YYYY/MM/DD HH:mm'
        )
      });
    }
  },
  methods: {
    async removeMember(member) {
      if (member.id === this.$store.state.client.client) {
        this.leaveRoom();
      } else {
        let res = await this.confirm(
          this.$i18n.t('Are you sure you want to remove {name}?', {
            name: member.nickname || this.$i18n.t('Unknown Name')
          })
        );

        if (res === 1) {
          this.deleteMember(member.id);
        }
      }
    },
    async leaveRoom() {
      let res = await this.confirm(
        this.$i18n.t('Are you sure you want to leave the group?')
      );
      if (res === 1) {
        this.deleteMember(this.$store.state.client.client);
      }
    },
    muteOnClick() {
      this.setRoomMuted({
        roomId: this.room._id,
        muted: !this.room.pref.muted
      });
    },
    botOnClick() {
      this.updateRoomInfo({
        roomId: this.room._id,
        roomInfo: {
          botMode: !this.room.botMode
        }
      });
    },
    back() {
      this.$emit('back');
    },
    showRoomCerate() {
      let find = this.room.members.find(member => {
        return member.id !== this.$store.state.client.client;
      });
      let invitee = find ? find.id : '';
      this.$emit('showRoomCerate', invitee);
    },
    getIframeUrl(url) {
      let query = {};
      let shap = '';
      if (~url.indexOf('?')) {
        let temp = url.split('?');
        let queryString = temp[1];
        url = temp[0];

        if (~queryString.indexOf('#')) {
          temp = queryString.split('#');
          queryString = temp[0];
          shap = temp[1];
        }
        query = qs.parse(queryString);
      }
      query.roomId = this.room.id;
      if (this.room.members) {
        query.clientIds = this.room.members.map(m => {
          return m.id;
        });
      }
      url = url + '?' + qs.stringify(query);
      if (shap) {
        url = url + '#' + shap;
      }
      return url;
    },
    ...mapActions(['confirm', 'deleteMember', 'setRoomMuted', 'updateRoomInfo'])
  }
};



