import { mapActions } from 'vuex';
import Modal from '@/components/Components/Modal/index.vue';
import Scrollbar from '@/components/Components/Scrollbar/index.vue';
import Radio from '@/components/Components/Radio/index.vue';

export default {
  name: 'SelectFolder',
  props: ['defaultSelectFolder'],
  data() {
    return {
      selectFolder: ''
    };
  },
  components: {
    Scrollbar,
    Radio,
    Modal
  },
  mounted() {
    this.selectFolder = this.defaultSelectFolder || '';
  },
  methods: {
    async save() {
      this.$emit('save', this.selectFolder);
      this.cancel();
    },
    cancel() {
      this.$emit('close');
    },
    ...mapActions(['addRoomFolderList'])
  }
};



