export default {
  vue: null,
  config: {
    debug: true,
    domain: '',
    authBase: '',
    authClientId: '',
    urlPreviewApi: 'https://chatkit.co/url',
    clientKey: '',
    token: '',
    privateKey: '',
    bucketName: '',
    googleApiKey: '',
    appLogo: 'https://i.imgur.com/gchEcBi.png',
    appName: 'IMKit Demo',
    lang: 'en',
    avatarHeaders: [],
    downloadFileHeaders: [],
    layout: {
      list: true,
      info: true
    },
    chat: {
      showHeader: true,
      actions: {
        emoji: {
          enable: true,
          mobileEnable: true,
          text: '傳送表情符號'
        },
        sticker: {
          enable: true,
          mobileEnable: true,
          text: '傳送貼圖'
        },
        image: {
          enable: true,
          mobileEnable: true,
          text: '傳送圖片',
          extra: {
            accept: ['image/png', 'image/jpeg'],
            limitSize: 5,
            thumbnailSize: 1500
          }
        },
        video: {
          enable: true,
          mobileEnable: true,
          text: '傳送影片',
          extra: {
            accept: ['video/mp4', 'video/quicktime'],
            limitSize: 5,
            thumbnailSize: 1500
          }
        },
        file: {
          enable: true,
          mobileEnable: true,
          text: '傳送檔案',
          extra: {
            accept: ['application/pdf'],
            limitSize: 5
          }
        },
        recorder: {
          enable: true,
          mobileEnable: true,
          text: '傳送語音',
          extra: {
            limitSeconds: 60
          }
        },
        location: {
          enable: true,
          mobileEnable: true,
          text: '傳送位置'
        },
        paymentRequest: {
          enable: false,
          mobileEnable: true,
          text: '請求付款',
          extra: {
            currencies: ['USD'],
            requestApi: '',
            paymentBy: []
          }
        }
      },
      actionsPosition: 'bottom',
      limitTextLength: 100,
      sliderReturnMode: 'close', // back or close
      asyncUrlPreview: true,
      colors: {
        header: {
          background: null,
          color: null
        },
        self: {
          background: null,
          color: null
        },
        other: {
          background: null,
          color: null
        },
        system: {
          background: null,
          color: null
        }
      },
      thumbnailSize: 500,
      readReceipt: true,
      reply: true,
      forward: true,
      recall: true,
      audioDownload: true
    },
    list: {
      sort: null,
      maxCount: null,
      lockText: '',
      lockOnClick: function() {},
      loadAllRoom: true,
      colors: {
        header: {
          background: null,
          color: null
        }
      },
      memberCount: true,
      logout: {
        enable: false,
        event: null
      },
      sticky: true,
      hidden: true,
      folder: true,
      createRoom: false
    },
    info: {
      colors: {
        header: {
          background: null,
          color: null
        }
      },
      edit: {
        room: {
          name: true,
          description: true,
          cover: true
        },
        leave: true,
        remove: true,
        createRoom: true,
        invite: true
      },
      openMembersList: false,
      iframes: []
    },
    events: {
      onSetRoomActiveDefault: true,
      onSetRoomActive: null,
      onAjaxError: () => {}
    },
    autoEnterRoom: true,
    autoEnterRoomMobile: false,
    defaultRoom: '',
    createRoom: {
      id: true,
      name: true,
      description: true,
      cover: true
    },
    alwaysMute: false,
    changeTitle: true,
    i18n: {
      en: {},
      'zh-tw': {}
    },
    special: {
      renderRoomDescription: null
    },
    notificationIcon: '',
    FCMConfig: null
  },
  // window 祖先，拉最上層改 title 及判斷分頁是否檢視中用
  pageAncestor: {
    el: null, // 祖先元件
    title: 'IMLit Demo' // 預設的 title
  },
  pageLoading: true, // 打開頁面時顯示載入中，直到列表載入完成
  api: {},
  socket: {},
  connect: true, // 是否與 socket 連接，判斷斷線用，初始設 true 避免頁面一載入就顯示斷線訊息
  i18n: {},
  alert: null,
  confirm: null,
  roomList: [],
  roomFolderList: [], // 房間資料夾
  roomListLoader: true, // 是否顯示圈圈
  roomListLoading: false, // 是否正在 call API
  roomStickyList: [], // 置頂的房間
  roomChatList: [],
  roomActive: {},
  sendingList: [],
  client: {},
  forward: {
    messages: [],
    rooms: [],
    selectMessages: false,
    selectRooms: false,
    sending: false
  },
  typingList: [],
  supportNotification: false, // 是否支援推播
  enableNotification: false, // 是否開啟推播
  isMobile: false, // 是否有 touch 事件
  widthPad: 1024, // Pad 版對大寬度
  widthPhone: 640 // Phone 版對大寬度
};



