import { mapActions } from 'vuex';
import Scrollbar from '@/components/Components/Scrollbar/index.vue';
import RoomListItem from '@/components/RoomList/RoomListItem/index.vue';
import AddFolder from './AddFolder/index.vue';
import SelectFolder from './SelectFolder/index.vue';
import RoomFolder from './RoomFolder/index.vue';

export default {
  name: 'RoomList',
  data() {
    return {
      searchText: '',
      list: null,
      isShowAddFolder: false,
      isShowSelectFolder: false,
      defaultSelectFolder: null,
      isHideActions: true,
      hideActionsTimer: null,
      isDrag: false,
      dragoverFolder: null,
      dragRoom: null
    };
  },
  components: {
    AddFolder,
    SelectFolder,
    RoomFolder,
    RoomListItem,
    Scrollbar
  },
  computed: {
    listScroll() {
      return this.$refs.list;
    },
    roomFolderList() {
      return this.$store.state.roomFolderList
        .map(folder => {
          folder.sortIdx = this.$store.state.roomList.findIndex(room => {
            return room.folder === folder.id;
          });
          if (folder.sortIdx === -1) {
            folder.sortIdx = 999999;
          }
          folder.unread = this.$store.state.roomList.reduce((prev, room) => {
            return prev + (room.folder === folder.id ? room.unread : 0);
          }, 0);
          return folder;
        })
        .sort((a, b) => {
          return a.sortIdx > b.sortIdx ? 1 : -1;
        });
    }
  },
  mounted() {
    this.hideActions();
  },
  methods: {
    filterSearch(room) {
      if (!this.searchText) {
        return true;
      } else {
        let searchText = this.searchText.toLowerCase();
        if (room.name && ~room.name.toLowerCase().indexOf(searchText)) {
          return true;
        } else if (room.members) {
          let find = room.members.find(member => {
            return (
              member.nickname &&
              ~member.nickname.toLowerCase().indexOf(searchText)
            );
          });
          if (find) {
            return true;
          }
        }
      }
      return false;
    },
    onListScroll() {
      if (
        !this.searchText &&
        this.$store.state.roomList &&
        !this.$store.state.roomListLoading && // 正在讀取中
        this.$store.state.roomListLoader && // 有更多
        this.listScroll.scrollBottom() < 10
      ) {
        this.getRoomList();
      }
      // 隱藏 add 按鈕
      this.hideActions();
    },
    hideActions() {
      if (this.hideActionsTimer) {
        clearTimeout(this.hideActionsTimer);
        this.hideActionsTimer = null;
      }
      this.isHideActions = true;
      this.hideActionsTimer = setTimeout(() => {
        this.isHideActions = false;
      }, 500);
    },
    notificationsOnClick() {
      this.$fcm
        .requestPermission()
        .then(() => {
          this.$fcm.getToken().then(refreshedToken => {
            if (this.$store.state.enableNotification) {
              // 關閉
              this.unsubscribe(refreshedToken);
            } else {
              // 開啟
              this.subscribe(refreshedToken);
            }
          });
        })
        .catch(error => {
          this.$log.error('[FCM requestPermission]', error);
        });
    },
    logout() {
      let logoutEvent = this.$store.state.config.list.logout.event;
      if (this.$store.state.enableNotification) {
        this.$fcm.getToken().then(refreshedToken => {
          this.unsubscribe(refreshedToken);
          if (logoutEvent) {
            logoutEvent();
          }
        });
      } else {
        if (logoutEvent) {
          logoutEvent();
        }
      }
    },
    roomClick(room) {
      if (room.id !== this.$store.state.roomActive.id) {
        this.setRoomActive(room.id);
      }
    },
    dragstart(room) {
      this.dragRoom = room;
    },
    dragend() {
      this.dragRoom = null;
    },
    dragover(e, folderId) {
      e.preventDefault();
      this.dragoverFolder = folderId;
    },
    dragleave(e) {
      e.preventDefault();
      this.dragoverFolder = null;
    },
    dragdrop(e, folderId) {
      e.preventDefault();
      this.saveSelectFolder(folderId);
    },
    showSelectFolder(room) {
      this.isShowSelectFolder = true;
      this.dragRoom = room;
      this.defaultSelectFolder = room.folder;
    },
    saveSelectFolder(folderId) {
      this.dragRoom.folder = folderId;
      this.setRoomFolder({
        room: this.dragRoom,
        folder: folderId
      });
    },
    ...mapActions([
      'getRoomList',
      'subscribe',
      'unsubscribe',
      'setRoomActive',
      'setRoomFolder'
    ])
  }
};



