import store from '@/store';
import filters from '@/filters';
import i18n from '@/i18n';

let urlRegex = /([hH][tT][tT][pP]([sS]?):\/\/)?(\w+\.)+[A-Za-z]{2,}(\/\S+)?/gi;
let phoneRegex = /\d{8,256}/gi;

class Message {
  systemTypes = [
    'joinRoom',
    'leaveRoom',
    'addMember',
    'addMembers',
    'deleteMember',
    'recall'
  ];
  // 補足缺少的 key，缺少的 key 會導致資料更新時無法同步渲染
  async fullKeys(message) {
    let keys = [];
    for (let key of keys) {
      if (!(key in message)) {
        message[key] = null;
      }
    }

    if (!message.sender) {
      message.sender = {
        id: store.state.client.client,
        _id: store.state.client.client
      };
    }

    // 把有 url 的 textType 轉成urlType
    if (store.state.config.chat.asyncUrlPreview) {
      this.textToUrlType(message);
    } else {
      await this.textToUrlType(message);
    }
  }
  // 送出的時候轉換
  async textToUrlType(message) {
    if (message.messageType === 'text') {
      let urls = message.message.match(urlRegex);
      if (urls && urls.length > 0) {
        let originalUrl = [];
        let extra = [];
        let urlCache =
          JSON.parse(sessionStorage.getItem('IMKit-urlCache')) || [];
        for (let url of urls) {
          // 補 http://
          if (url.toLowerCase().indexOf('http') !== 0) {
            url = `http://${url}`;
          }
          // 有在cache裡，直接用，沒有再撈
          let urlData = null;
          let find = urlCache.find(item => {
            return item.url === url;
          });
          if (find) {
            urlData = find.data;
          } else {
            urlData = await store.state.api.url.getPreview(url);
            urlCache.push({
              url: url,
              data: urlData
            });
          }
          if (urlData) {
            originalUrl.push(url);
            extra.push(urlData);
          }
        }
        sessionStorage.setItem('IMKit-urlCache', JSON.stringify(urlCache));

        message.messageType = 'url';
        message.originalUrl = JSON.stringify(originalUrl);
        message.extra = JSON.stringify(extra);

        store.commit('checkScrollToBottom');
      }
    }
  }
  // 收到的訊息處理
  textView(message) {
    let _message = '';

    // 處理 tags
    if (message.extra) {
      let extra = JSON.parse(message.extra);
      if (extra && extra.tags) {
        let idx = 0;
        extra.tags.forEach(tag => {
          let findMember = store.state.roomActive.members.find(m => {
            return m._id === tag.uid;
          });
          if (findMember) {
            _message += this.XSS(message.message.substring(idx, tag.offset));
            _message += `<span class="textTag" data-uid="${btoa(
              findMember._id
            )}">@`;
            _message += this.XSS(findMember.nickname) || i18n.t('Unknown Name');
            _message += '</span>';
            idx = tag.offset + tag.length;
          }
        });
        _message += this.XSS(message.message.substr(idx));
      }
    }

    if (!_message) {
      _message = this.XSS(message.message);
    }

    // 處理網址
    let messageList = [];
    let searchIdx = _message.search(urlRegex);

    while (searchIdx >= 0) {
      if (searchIdx > 0) {
        messageList.push(
          _message.substr(0, searchIdx).replace(phoneRegex, str => {
            return `<a href="tel:${str}" target="_blank">${str}</a>`;
          })
        );
        _message = _message.substr(searchIdx);
      }
      _message = _message.replace(urlRegex, str => {
        let link = str;
        if (link.indexOf('http') !== 0) {
          link = `http://${link}`;
        }
        messageList.push(`<a href="${link}" target="_blank">${str}</a>`);
        return '';
      });

      searchIdx = _message.search(urlRegex);
    }
    messageList.push(
      _message.replace(phoneRegex, str => {
        return `<a href="tel:${str}" target="_blank">${str}</a>`;
      })
    );
    return messageList.join('');
  }

  // XSS
  XSS(str) {
    return str
      .replace(/ /g, '&nbsp;')
      .replace(/</g, '&lt;')
      .replace(/>/g, '&gt;')
      .replace(/\n/g, '<br />');
  }

  // 顯示訊息跨日時間
  messagesTimespan(messages) {
    let isFirst = true;
    let timespan = '';
    for (let message of messages) {
      message.timespan = null;
      if (message.messageTimeMS) {
        let _timespan = filters.timeDiff(message.messageTimeMS, true);
        if (_timespan !== timespan) {
          message.timespan = _timespan;
          // 如果是今天，後面就不用算了
          // 如果是今天而且是第一個 timespan，就不顯示
          if (_timespan === i18n.t('Today')) {
            if (isFirst) {
              message.timespan = null;
            }
            break;
          }
          timespan = _timespan;
          isFirst = false;
        }
      }
    }
  }
  addMembersMessage(message) {
    return i18n.t('{name} invites {members} to join.', {
      name: message.sender.nickname || i18n.t('Unknown Name'),
      members: JSON.parse(message.message)
        .map(member => {
          return member.nickname || i18n.t('Unknown Name');
        })
        .join(', ')
    });
  }
  // 縮成小訊息
  messageFormat(message, showSender = true) {
    let getSenderName = () => {
      return showSender && message.sender
        ? message.sender.nickname || i18n.t('Unknown Name')
        : '';
    };
    let getMemberName = () => {
      return message.member && message.member.nickname
        ? message.member.nickname
        : i18n.t('Unknown Name');
    };
    switch (message.messageType) {
      case 'url':
      case 'text':
        return message.message;
      case 'addMember':
      case 'joinRoom':
        return i18n.t('{name} joined room.', {
          name: getMemberName()
        });
      case 'addMembers':
        return this.addMembersMessage(message);
      case 'leaveRoom':
        return i18n.t('{name} leave room.', {
          name: getMemberName()
        });
      case 'deleteMember':
        return i18n.t('{sender} has removed {name}.', {
          sender: getSenderName(),
          name: getMemberName()
        });
      case 'sticker':
        return i18n.t('{name} send sticker.', {
          name: getSenderName()
        });
      case 'image':
        return i18n.t('{name} send image.', {
          name: getSenderName()
        });
      case 'video':
        return i18n.t('{name} send video.', {
          name: getSenderName()
        });
      case 'audio':
        return i18n.t('{name} send audio.', {
          name: getSenderName()
        });
      case 'file':
        return i18n.t('{name} send file.', {
          name: getSenderName()
        });
      case 'location':
        return i18n.t('{name} send location.', {
          name: getSenderName()
        });
      case 'recall':
        return i18n.t('{name} recall a message.', {
          name: getSenderName()
        });
      case 'encrypted':
        return i18n.t('{name} send an encrypted message.', {
          name: getSenderName()
        });
      case 'request_payment':
        return i18n.t('{name} send a payment request.', {
          name: getSenderName()
        });
      case 'payment':
        return i18n.t('{name} send a payment.', {
          name: getSenderName()
        });
      case 'template':
        return i18n.t('{name} send a template.', {
          name: getSenderName()
        });
      default:
        return i18n.t('Unsupported message.', {
          name: getSenderName()
        });
    }
  }
}

export default Message;



