import { mapActions } from 'vuex';
import Modal from '@/components/Components/Modal/index.vue';

export default {
  name: 'RoomInfoInvite',
  props: ['roomId'],
  data() {
    return {
      invitee: null
    };
  },
  components: {
    Modal
  },
  mounted() {},
  methods: {
    async invite() {
      let invitees = this.invitee.replace(/\s/, '').split(',');
      await this.addMembers(invitees);
      this.$emit('close');
    },
    cancel() {
      this.$emit('close');
    },
    ...mapActions(['addMembers'])
  }
};



