import { mapActions } from 'vuex';
import Modal from '@/components/Components/Modal/index.vue';

export default {
  name: 'AddFolder',
  props: [],
  data() {
    return {
      name: ''
    };
  },
  components: {
    Modal
  },
  mounted() {},
  methods: {
    async create() {
      this.addRoomFolderList(this.name);
      this.cancel();
    },
    cancel() {
      this.$emit('close');
    },
    ...mapActions(['addRoomFolderList'])
  }
};



