import Vue from 'vue';
import VueI18n from 'vue-i18n';
import store from '@/store';
import en from './en.json';
import zhTw from './zh-tw.json';

const i18nMessages = {
  en: en,
  'zh-tw': zhTw
};

Vue.use(VueI18n);

const i18n = new VueI18n({
  locale: 'en',
  fallbackLocale: 'en',
  messages: i18nMessages
});
store.commit('setI18n', i18n);

export default i18n;



