import Modal from '@/components/Components/Modal/index.vue';

export default {
  name: 'AlertConfirm',
  props: [],
  data() {
    return {
      type: ''
    };
  },
  components: {
    Modal
  },
  computed: {
    data() {
      if (this.$store.state.alert) {
        this.type = 'alert';
        return this.$store.state.alert;
      } else if (this.$store.state.confirm) {
        this.type = 'confirm';
        return this.$store.state.confirm;
      } else {
        return null;
      }
    }
  },
  methods: {
    ok() {
      if (this.data.ok) {
        this.data.ok();
      }
      this.close();
    },
    cancel() {
      if (this.data.cancel) {
        this.data.cancel();
      }
      this.close();
    },
    close() {
      switch (this.type) {
        case 'alert':
          this.$store.commit('setAlert', null);
          break;
        case 'confirm':
          this.$store.commit('setConfirm', null);
          break;
      }
    }
  }
};



