import axios from 'axios';

export default {
  name: 'Avatar',
  props: ['name', 'avatar'],
  data() {
    return {
      avatarImages: null
    };
  },
  computed: {
    avatarType() {
      return typeof this.avatar;
    }
  },
  watch: {
    avatar: {
      handler: function() {
        this.getImages();
      },
      deep: true
    }
  },
  mounted() {
    this.getImages();
  },
  methods: {
    isShowImage(url) {
      return (
        !this.$store.state.config.avatarHeaders ||
        !this.$store.state.config.avatarHeaders.length ||
        url.indexOf('blob') === 0
      );
    },
    async getImages() {
      // 圖片要帶 headers，必須透過 AJAX GET
      if (
        this.$store.state.config.avatarHeaders &&
        this.$store.state.config.avatarHeaders.length
      ) {
        if (this.avatarType === 'string') {
          this.avatarImages = await this.getImage(this.avatar);
        } else if (this.avatarType === 'object') {
          let avatarImages = [];
          for (let idx in this.avatar) {
            avatarImages[idx] = await this.getImage(this.avatar[idx]);
          }
          this.avatarImages = avatarImages;
        }
      } else {
        this.avatarImages = this.avatar;
      }
    },
    getImage(url) {
      return new Promise((resolve, reject) => {
        if (window.sessionStorage[`IMKit-${url}`]) {
          // 已經撈過 cache 了
          resolve(window.sessionStorage[`IMKit-${url}`]);
        }

        let headers = {};
        this.$store.state.config.avatarHeaders.forEach(item => {
          headers[item.name] =
            item.type === 'variable'
              ? this.$store.state.config[item.value]
              : item.value;
        });

        axios
          .get(url, {
            headers: headers,
            responseType: 'arraybuffer'
          })
          .then(
            res => {
              if (res.status >= 200 && res.status < 400) {
                let blob = new Blob([new Uint8Array(res.data)], {
                  type: res.headers['content-type']
                });
                let objectURL = URL.createObjectURL(blob);
                window.sessionStorage[`IMKit-${url}`] = objectURL;
                resolve(objectURL);
              }
            },
            () => {
              // 失敗，返回原值
              resolve('');
            }
          );
      });
    }
  }
};



