import { mapActions } from 'vuex';
import Modal from '@/components/Components/Modal/index.vue';

export default {
  name: 'Location',
  props: ['action'],
  data() {
    return {
      actionName: 'location',
      disabledSend: true,
      mapOption: {
        streetViewControl: false,
        fullscreenControl: false,
        mapTypeId: 'roadmap',
        zoom: 15
      },
      mapCenter: {
        lat: 25.033976,
        lng: 121.564471
      },
      markerPos: {
        lat: 25.033976,
        lng: 121.564471
      }
    };
  },
  components: {
    Modal
  },
  directives: {},
  computed: {},
  mounted() {},
  methods: {
    open() {
      if ('geolocation' in navigator) {
        navigator.geolocation.getCurrentPosition(
          pos => {
            let { latitude, longitude } = pos.coords;
            this.mapCenter.lat = this.markerPos.lat = latitude;
            this.mapCenter.lng = this.markerPos.lng = longitude;
          },
          error => {
            this.alert(this.$i18n.t('Failed to get location'));
            this.$log.error('[getCurrentPosition]', error);
          }
        );
      } else {
        this.alert(this.$i18n.t('Your browser does not support geolocation'));
      }
      this.$emit('open', this.actionName);
    },
    close() {
      if (this.action === this.actionName) {
        this.$emit('close');
      }
    },
    centerChange(e) {
      this.markerPos.lat = e.lat();
      this.markerPos.lng = e.lng();
    },
    mapLoad() {
      this.disabledSend = false;
    },
    goOrigin() {
      this.$refs.mapRef.$mapPromise.then(map => {
        map.panTo(this.mapCenter);
      });
    },
    async send() {
      if (this.disabledSend) {
        return;
      }
      this.disabledSend = true;
      let precision = Math.pow(10, 8);
      let address = await this.getAddress(this.markerPos);
      let msg = {
        messageType: 'location',
        message: address,
        latitude: Math.round(this.markerPos.lat * precision) / precision,
        longitude: Math.round(this.markerPos.lng * precision) / precision
      };
      this.$emit('outputScrollToBottom');
      this.$emit('send', msg);
      this.$emit('close');
    },
    getAddress(latlng) {
      return new Promise((resolve, reject) => {
        const geocoder = new window.google.maps.Geocoder();

        geocoder.geocode({ location: latlng }, (results, status) => {
          if (results && results[0]) {
            resolve(results[0].formatted_address);
          } else {
            resolve('');
          }
        });
      });
    },
    ...mapActions(['alert'])
  }
};



