import Avatar from '@/components/Components/Avatar/index.vue';
import Checkbox from '@/components/Components/Checkbox/index.vue';
import ClickOutside from 'vue-click-outside';
import { mapActions } from 'vuex';

export default {
  name: 'RoomListItem',
  props: [
    'room',
    'active',
    'isShowTime',
    'isShowUnread',
    'isShowCheckbox',
    'isShowThumbtack',
    'checked',
    'lock'
  ],
  data() {
    return {
      isShowActionsList: false
    };
  },
  components: {
    Avatar,
    Checkbox
  },
  directives: {
    ClickOutside
  },
  computed: {
    isShowAction() {
      return this.isShowSticky || this.isShowHide;
    },
    isShowSticky() {
      return this.$store.state.config.list.sticky;
    },
    isShowHide() {
      return this.$store.state.config.list.hidden;
    },
    isSticky() {
      return ~this.$store.state.roomStickyList.findIndex(
        r => r.id === this.room.id
      );
    }
  },
  methods: {
    roomClick() {
      if (this.lock) {
        this.$store.state.config.list.lockOnClick();
      } else {
        this.$emit('roomClick');
      }
    },
    hideActionsList() {
      this.isShowActionsList = false;
    },
    ...mapActions(['setRoomSticky', 'setRoomHidden'])
  }
};



