import { mapActions } from 'vuex';
import query from 'dom-helpers/query';
import At from 'vue-at';
import Avatar from '@/components/Components/Avatar/index.vue';
import FileLoader from '@/components/Components/FileLoader/index.vue';
import DivEditor from '@/components/Components/DivEditor/index.vue';
import Emoji from '@/components/RoomChat/RoomChatInput/RoomChatInputActions/Emoji/index.vue';
import Sticker from '@/components/RoomChat/RoomChatInput/RoomChatInputActions/Sticker/index.vue';
import ImageUpload from '@/components/RoomChat/RoomChatInput/RoomChatInputActions/ImageUpload/index.vue';
import VideoUpload from '@/components/RoomChat/RoomChatInput/RoomChatInputActions/VideoUpload/index.vue';
import FileUpload from '@/components/RoomChat/RoomChatInput/RoomChatInputActions/FileUpload/index.vue';
import Location from '@/components/RoomChat/RoomChatInput/RoomChatInputActions/Location/index.vue';
import Recorder from '@/components/RoomChat/RoomChatInput/RoomChatInputActions/Recorder/index.vue';
import PaymentRequest from '@/components/RoomChat/RoomChatInput/RoomChatInputActions/PaymentRequest/index.vue';

export default {
  name: 'RoomChatInput',
  data() {
    return {
      reply: null,
      inputText: '',
      action: ''
    };
  },
  components: {
    At,
    Avatar,
    FileLoader,
    DivEditor,
    Emoji,
    Sticker,
    ImageUpload,
    VideoUpload,
    FileUpload,
    Recorder,
    Location,
    PaymentRequest
  },
  computed: {
    VERSION() {
      return process.env.VERSION;
    },
    GIT_COMMIT_HASH() {
      return process.env.GIT_COMMIT_HASH.trim();
    },
    typingString() {
      let typingList = this.$store.state.typingList;
      let roomActive = this.$store.state.roomActive;
      if (!typingList.length) {
        return '';
      }
      let filter = typingList
        .filter(typing => {
          return typing.room === roomActive.id;
        })
        .map(typing => {
          let user = roomActive.members.find(member => {
            return member.id === typing.sender;
          });
          if (!user || !user.nickname) {
            return typing.sender;
          } else {
            return user.nickname;
          }
        })
        .filter((typing, index, arr) => {
          return arr.indexOf(typing) === index;
        })
        .sort((a, b) => {
          return a > b ? 1 : -1;
        });

      if (filter && filter.length === 1) {
        return this.$i18n.t('{name} is typing', {
          name: filter[0]
        });
      } else if (filter && filter.length > 1 && filter && filter.length <= 4) {
        return this.$i18n.t('{name} are typing', {
          name: filter.join(', ')
        });
      } else if (filter && filter.length > 4) {
        return this.$i18n.t('Many people are typing');
      } else {
        return '';
      }
    },
    members() {
      if (
        !this.$store.state.roomActive ||
        !this.$store.state.roomActive.members
      ) {
        return [];
      }
      return this.$store.state.roomActive.members.map(member => {
        return {
          uid: member._id,
          nickname: member.nickname || this.$t('Unknown Name'),
          avatarUrl: member.avatarUrl
        };
      });
    }
  },
  mounted() {
    // 送typing
    setInterval(() => {
      if (
        document.activeElement &&
        query.closest(document.activeElement, '.divEditorText') &&
        this.inputText
      ) {
        this.sendTyping(this.inputText);
      }
    }, 5000);

    this.$store.subscribe((mutation, state) => {
      switch (mutation.type) {
        // setRoomActive 的時候，取回 input 的 cache
        case 'setRoomActive':
          if (mutation.payload.id) {
            this.$nextTick(() => {
              this.init();
            });
          }
          break;
      }
    });
  },
  methods: {
    init() {
      let inputCache = localStorage.getItem(
        `IMKit-input-${this.$store.state.roomActive.id}`
      );
      if (inputCache) {
        this.setInput(inputCache || '');
      } else {
        this.setInput('');
      }
      let reply = localStorage.getItem(
        `IMKit-reply-${this.$store.state.roomActive.id}`
      );
      if (reply) {
        this.setReply(JSON.parse(reply) || null);
      } else {
        this.setReply(null);
      }
    },
    resize() {},
    openAction(action) {
      if (this.action !== action) {
        setTimeout(() => {
          this.action = action;
        }, 1);
      }
    },
    closeAction() {
      this.action = '';
    },
    setReply(message) {
      if (!this.$refs.divEditor || !this.$store.state.config.chat.reply) {
        return;
      }
      // 這行是為了把 FileLoader 砍掉重建，不然圖片不會更新
      if (this.reply) {
        this.reply.messageType = '';
      }
      setTimeout(() => {
        this.reply = Object.assign({}, message);
        localStorage.setItem(
          `IMKit-reply-${this.$store.state.roomActive.id}`,
          JSON.stringify(this.reply)
        );
      }, 0);
      if (!this.$store.state.isMobile) {
        this.$refs.divEditor.insertText('');
      }
    },
    setInput(text) {
      if (!this.$refs.divEditor) {
        return;
      }
      this.$refs.divEditor.setText(text);
    },
    inputChange(text) {
      if (text.length === 1 && text.charCodeAt(0) === 10) {
        text = '';
      }
      this.inputText = text;

      localStorage.setItem(
        `IMKit-input-${this.$store.state.roomActive.id}`,
        text
      );
    },
    insertEmoji(emoji) {
      this.$refs.divEditor.insertText(emoji);
    },
    send() {
      this.$refs.divEditor.changeText();

      // 把 tag 轉出來
      let div = document.createElement('div');
      div.innerHTML = this.inputText;
      let tags = div.querySelectorAll('[data-at-embedded]');
      let tagObjs = [];
      tags.forEach(tag => {
        let ele = tag.querySelector('.inputTextTag');
        let newSpan = document.createElement('span');
        newSpan.innerText = tagObjs.length;
        div.replaceChild(newSpan, tag);
        tagObjs.push({
          uid: ele.dataset.uid,
          nickname: ele.innerText
        });
      });
      let msgStr = div.innerHTML;
      tagObjs.forEach((tagObj, idx) => {
        let eleStr = `<span>${idx}</span>`;
        tagObj.offset = msgStr.indexOf(eleStr);
        tagObj.length = tagObj.nickname.length;
        msgStr = msgStr.replace(eleStr, tagObj.nickname + ' ');
        delete tagObj.nickname;
      });

      // 訊息為空就退出
      if (msgStr === '') {
        return;
      }
      // 暗號
      let command = msgStr.toLowerCase();
      if (command === 'tell me imkit version') {
        this.alert(`${this.VERSION}(${this.GIT_COMMIT_HASH})`);
        this.setInput('');
        return;
      }
      let msg = {
        messageType: 'text',
        message: msgStr,
        reply: this.reply && this.reply.id ? this.reply.id : null
      };
      if (tagObjs.length) {
        msg.extra = JSON.stringify({
          tags: tagObjs
        });
      }
      this.sendMessage(msg);
      this.setInput('');
      this.setReply(null);
      this.$nextTick(() => {
        this.$emit('outputScrollToBottom');
      });
    },
    outputScrollToBottom() {
      this.$emit('outputScrollToBottom');
    },
    ...mapActions(['alert', 'sendMessage', 'sendTyping'])
  }
};



