import ClickOutside from 'vue-click-outside';
import Scrollbar from '@/components/Components/Scrollbar/index.vue';

export default {
  name: 'Sticker',
  props: ['action'],
  data() {
    return {
      actionName: 'sticker',
      stickers: Array.from({ length: 40 }).map(
        (k, i) => `FunFunFamily-${i + 1}`
      )
    };
  },
  components: {
    Scrollbar
  },
  directives: {
    ClickOutside
  },
  computed: {},
  methods: {
    open() {
      this.$emit('open', this.actionName);
    },
    close() {
      if (this.action === this.actionName) {
        this.$emit('close');
      }
    },
    send(sticker) {
      let msg = {
        messageType: 'sticker',
        sticker: sticker
      };
      this.$emit('outputScrollToBottom');
      this.$emit('send', msg);
      this.$emit('close');
    }
  }
};



