import { mapActions } from 'vuex';
import RoomList from '@/components/RoomList/index.vue';
import RoomInfo from '@/components/RoomInfo/index.vue';
import RoomChat from '@/components/RoomChat/index.vue';

import AlertConfirm from '@/components/Components/AlertConfirm/index.vue';
import RoomCreateModal from '@/components/Home/RoomCreateModal/index.vue';

export default {
  name: 'Home',
  data() {
    return {
      isShowRoomCerate: false,
      roomCerateInvitee: null,
      views: {
        pc: {
          info: true
        },
        mobile: {
          info: false
        }
      }
    };
  },
  components: {
    RoomList,
    RoomInfo,
    RoomChat,
    AlertConfirm,
    RoomCreateModal
  },
  computed: {
    listClass() {
      return {
        phoneHide: this.$store.state.roomActive.id
      };
    },
    chatClass() {
      return {
        pcHide: !this.$store.state.roomActive.id,
        padHide: this.views.mobile.info || !this.$store.state.roomActive.id,
        phoneHide: this.views.mobile.info
      };
    },
    infoClass() {
      return {
        pcHide: !this.views.pc.info,
        padHide: !this.views.mobile.info,
        phoneHide: !this.views.mobile.info
      };
    }
  },
  methods: {
    hideChat() {
      this.setRoomActive(null);
      this.views.mobile.info = false;
    },
    hideInfo() {
      this.views.pc.info = false;
      this.views.mobile.info = false;
    },
    toggleInfo() {
      this.views.pc.info = !this.views.pc.info;
      this.views.mobile.info = !this.views.mobile.info;
    },
    showRoomCerate(invitee = null) {
      this.roomCerateInvitee = invitee;
      this.isShowRoomCerate = true;
    },
    ...mapActions(['setRoomActive'])
  }
};



